.matches
    display: grid
    grid-template-rows: 1fr
    grid-template-columns: 1fr
    row-gap: 1rem

button
    width: 100px
    height: 100px

@media screen and (min-width: 47.9rem) /* tablet*/

    .match
        display: grid
        grid-template-columns: repeat(3, 1fr)
        justify-items: center

@media screen and (max-width: 47.9rem) /* tablet*/

    .match
        display: flex
        justify-content: space-between
        gap: 0.5rem